<template>
  <el-container class="select-el-container">
    <el-header class="select-el-header">
      <el-page-header @back="goBack" content="综合知识"/>
    </el-header>
    <el-main class="select-el-main">

      <el-container>
        <el-header>
          <exam-head @btnClick="start" ref="selectHead">
            <span slot="btnName">开始考试</span>
          </exam-head>
        </el-header>
        <el-container>
          
          <el-main>
            <select-question 
            :ccurrentChoiceQuestion="currentChoiceQuestion" 
            :ccurrentchoiceId="currentchoiceId"
            :cradioClickEvent="radioClickEvent"
            @radioClick="getUserAnswer"
            @finish="finishExam"
            ref="select"
            />
            
          </el-main>
          <el-aside width="36%" class="select-el-progress" v-show="currentChoiceQuestion.length !== 0">
            <!-- 进度 -->
            <Progress :cpercentage="percentage"/>
            
            <!-- 计时器 -->
            <span class="timer">{{ currentTime | getHour | fixTime }}:{{ currentTime | getMinute | fixTime }}:{{ currentTime | getSecond | fixTime }}</span>
          </el-aside>
        </el-container>
      </el-container>

    </el-main>
  </el-container>
</template>

<script>
  import axios from 'axios'
  import ExamHead from './childComps/ExamHead'
  import SelectQuestion from './childComps/SelectQuestion'
  import Progress from './childComps/Progress'
  export default {
  name: 'MainSelect',
  components: {
    ExamHead,
    SelectQuestion,
    Progress
  },
  data() {
    return {
      currentOption: [],
      currentChoiceQuestion: [],
      currentchoiceId: 1,
      percentage: 0,
      userAnswer: [],
      paperAnswer: [],
      radioClickEvent: [],
      timer: '',
      currentTime: 7200,
      startDate: '',
      endDate: ''
    }
  },
  props: {
    cquestionInfo: {
      type: Object,
      default() {
        return {}
      }
    },
    cerrorBookInfo: {
      type: Object,
      default() {
        return {}
      }
    },
    ccurrentPersonMsg: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    start() {
      this.resetValue()
      this.currentOption = this.$refs.selectHead.optionValue[0] + this.$refs.selectHead.optionValue[1]
      this.getQuestionInfo()
      this.getPaperAnswer()
      this.createTimer()
      this.startDate = this.getCurrentTime()
    },
    getQuestionInfo() {
      // 筛选出需要的试卷题目信息
      for(let i of this.cquestionInfo.paperInfo) {
        if(i.courseName === this.currentOption) {
          this.currentChoiceQuestion = i.choiceQuestion
        }
      }
    },
    getUserAnswer(e, id) {
      // 保存用户的点击事件
      this.radioClickEvent.push(e)
      // 保存用户的答题
      this.userAnswer[id - 1] = e.target.value 
      // 计算答题进度
      this.calcPercentage()
    },
    getPaperAnswer() {
      for(let i of this.currentChoiceQuestion) {
        this.paperAnswer.push(i.answer)
      }
    },
    calcPercentage() {
      this.percentage = parseInt((this.userAnswer.length / this.currentChoiceQuestion.length) * 100)
    },
    resetValue() {
      //重置值
      this.currentChoiceQuestion = []
      this.$refs.select.currentchoiceId = 1
      this.percentage = 0
      this.userAnswer = []
      this.paperAnswer = []
      this.startDate = ''
      this.endDate = ''
      this.currentTime = 7200
      clearTimeout(this.timer)
      // 重置radio状态
      for(let i of this.radioClickEvent) {
        if(i !== '') i.target.checked = ''
      }
    },
    finishExam() {
      if(this.userAnswer.length === this.paperAnswer.length) {
        this.endDate = this.getCurrentTime()
        let score = this.countScore()
        this.$message({showClose: true, message: '恭喜你完成了本次答题, 得分为  ' + score, type: 'success'})
        clearTimeout(this.timer)
      }else {
        this.$message({showClose: true, message: '试题尚未全部完成, 请继续完成试题', type: 'warning'})
      }
    },
    countScore() {
      let score = 0
      let errorNum = 0
      let arr = []
      for(let i = 0; i < this.userAnswer.length; i++) {
        if(this.userAnswer[i] === this.paperAnswer[i]) {
          score += 1
          arr[i] = 'right'
        }else {
          arr[i] = 'wrong'
          errorNum += 1
        }
      }
      this.putPersonMsg(score)
      this.putErrorSet(arr, errorNum)
      return score
    },
    putErrorSet(arr, errorNum) {
      for(let i = 0; i < arr.length; i++) {
        if(arr[i] === 'wrong') {    // 找到做错的题目
          for(let j of this.cerrorBookInfo.errorSet) {
            if(this.currentChoiceQuestion[i].question === j.question) {    // 错题本中已经存在该题目, 将错误的次数 + 1
              j.frequency += 1
              arr[i] = 'done'
            }
          }
          // 错题本中没有该题目 创建模板并保存数据
          if(arr[i] === 'wrong') {
            let temp_error = {}
            temp_error.frequency = 1
            temp_error.question = this.currentChoiceQuestion[i].question
            temp_error.chose = this.currentChoiceQuestion[i].chose
            temp_error.answer = this.currentChoiceQuestion[i].answer
            this.cerrorBookInfo.errorSet.push(temp_error)
          }
        }
      }
      this.cerrorBookInfo.totalError += errorNum
      return axios.put('http://localhost:3000/ErrorBook/' + this.$store.state.currentUserMsg.id, this.cerrorBookInfo)
    },
    createTimer() {
      let _this = this
      this.timer = setInterval(() => {
        if(_this.currentTime != 0) {
          _this.currentTime--
        }else {
          _this.currentTime = 0
          alert('时间到')
          clearTimeout(this.timer)
        }
      }, 1000);
    },
    getCurrentTime() {
        let time = new Date()
        let Y = time.getFullYear()    //年
        let M = this.$options.filters['fixTime'](time.getMonth() + 1)       //月
        let D = this.$options.filters['fixTime'](time.getDate())        //日
        let h = this.$options.filters['fixTime'](time.getHours())       //小时
        let m = this.$options.filters['fixTime'](time.getMinutes())     //分钟
        let s = this.$options.filters['fixTime'](time.getSeconds())     //秒
        let val = Y + '-' + M + '-' + D + '     ' + h + ':' + m + ':' + s
        return val
    },
    putPersonMsg(score) {
      let temp_data = {}
      this.ccurrentPersonMsg.scoreInfo.length === 0 ? temp_data.order = 1 : temp_data.order = this.ccurrentPersonMsg.scoreInfo.slice(-1)[0].order + 1
      temp_data.category = this.currentOption
      temp_data.endDate = this.endDate
      temp_data.startDate = this.startDate
      temp_data.score = score //返回计算的分数
      this.ccurrentPersonMsg.scoreInfo.push(temp_data)
      return axios.put('http://localhost:3000/PersonMessage/' + this.$store.state.currentUserMsg.id, this.ccurrentPersonMsg)
    }
  },
  filters: {
    getHour(e) {
      e = e / 60 / 60 % 24
      return parseInt(e)
    },
    getMinute(e) {
      e = e / 60 % 60 < 10 ? '0' + e / 60  % 60 : e / 60 % 60
      return parseInt(e)
    },
    getSecond(e) {
      e = e % 60 < 10 ? '0' + e % 60 : e % 60
      return parseInt(e)
    },
    fixTime(e) {
      e < 10 ? e = '0' + e : e
      return e
    }
  }
}
</script>

<style>
  .select-el-main {
    margin: 0 32px;
  }
  .question-el-main {
    width: 555px;
  }
  .select-answer-card {
    display: flex;
    flex-wrap: wrap;
  }
  .timer {
    text-align: center;
  }
  .select-question-button {
    margin: 20px;
  }
</style>